<template>
    <v-sheet color="transparent" class="defaultCard">
        <h1 class="mb-6">
            {{ t('reportPage.title')}} 
            <span class="dateTitle" v-if="dateini">[ {{ dateini }}</span> 
            <span class="dateTitle" v-if="dateini && datefin"> - {{ datefin }}</span> 
            <span class="dateTitle" v-if="dateini"> ]</span>
        </h1>
        <v-row>
            <v-col cols="12" sm="12" md="6" lg="4" xl="4" class="py-0">
                <h4 class="mb-2">{{ t('datePickers.dateini')}}</h4>
                <VueDatePicker 
                class="mb-4"
                v-model="dateini" 
                :model-type="CONSTSTRINGS.STRINGS_HTML.time" 
                :format="CONSTSTRINGS.STRINGS_HTML.time" />
            </v-col>
            <v-col cols="12" sm="12" md="6" lg="4" xl="4" class="py-0">
                <h4 class="mb-2">{{ t('datePickers.datefin')}}</h4>
                <VueDatePicker 
                class="mb-4"
                v-model="datefin" 
                :model-type="CONSTSTRINGS.STRINGS_HTML.time" 
                :format="CONSTSTRINGS.STRINGS_HTML.time" />
            </v-col>
            <v-col cols="12" sm="12" md="6" lg="4" xl="4" class="py-0">
                <h4 class="mb-1">{{ t('labels.alarm')}} </h4>
                <v-select
                v-model="alarm"
                :items="itemsAlert"
                item-title="title"
                item-value="value"
                density="compact"
            ></v-select>
            </v-col>
            <v-col cols="12" sm="12" md="4" lg="4" xl="4" xxl=4 class="py-0">
                <h4 class="mb-1">{{ t('labels.distribution')}}</h4>
                <v-select
                v-model="ipsDistri"
                :items="itemsIPDistri"
                item-title="title"
                item-value="value"
                density="compact"
                multiple
                single-line>
                
                <template v-slot:selection="{ item, index }">
                    <v-chip v-if="index < 2">
                        <span>{{ item.title }}</span>
                    </v-chip>
                    <span
                    v-if="index === 2"
                    class="text-grey text-caption align-self-center"
                    >
                        (+{{ ipsDistri.length - 2 }} {{ t('reportPage.others')}})
                    </span>
                </template>
                </v-select>
            </v-col>
            <v-col cols="12" sm="12" md="4" lg="4" xl="4" xxl=4 class="py-0" >
                <h4 class="mb-1">{{ t('labels.client')}}</h4>
                <v-select
                v-model="ipsClient"
                :items="itemsIPClient"
                item-title="title"
                item-value="value"
                density="compact"
                multiple
                single-line>
                
                <template v-slot:selection="{ item, index }">
                    <v-chip v-if="index < 2">
                        <span>{{ item.title }}</span>
                    </v-chip>
                    <span
                    v-if="index === 2"
                    class="text-grey text-caption align-self-center"
                    >
                        (+{{ ipsClient.length - 2 }} {{ t('reportPage.others')}})
                    </span>
                </template>
                </v-select>
            </v-col>
            <v-col cols="12" sm="12" md="4" lg="4" xl="4" xxl=4 class="py-0" >
                <h4 class="mb-1">{{ t('labels.access')}}</h4>
                <v-select
                v-model="ipsAcceso"
                :items="itemsIPAcceso"
                item-title="title"
                item-value="value"
                density="compact"
                multiple
                single-line>
                
                <template v-slot:selection="{ item, index }">
                    <v-chip v-if="index < 2">
                        <span>{{ item.title }}</span>
                    </v-chip>
                    <span
                    v-if="index === 2"
                    class="text-grey text-caption align-self-center"
                    >
                        (+{{ ipsAcceso.length - 2 }} {{ t('reportPage.others')}})
                    </span>
                </template>
                </v-select>
            </v-col>            
        </v-row>
        <v-row>
            <v-btn 
            :disabled="!(datefin > dateini) || alarm === '' || selectedIP.length === 0" 
            v-if="disableBTN"
            class="ml-4 mb-6" 
            color="btnSelect"
            @click="getItems">
            {{ t('buttons.searchbtn')}}
            </v-btn>
            <v-btn v-else class="ml-4 mb-6" color="btnSelect" disabled >
                {{ t('buttons.searchbtn')}}
            </v-btn>
            <!-- <v-btn class="ml-4 mb-6" color="btnSelect" v-if="allItems.length>0" @click="showMap">
                {{ t('mapsPage.showMap')}}
            </v-btn> -->
        </v-row>
        <v-sheet v-if="showTable" class="defaultCard" color="transparent">
            <v-sheet v-if="allItems.length > 0" color="transparent">
                <EasyDataTable
                table-class-name="customize-table"
                :headers="header"
                :items="allItems"
                show-index
                >
                <template #item-detalle ="item">
                    {{
                        alarmTable === CONSTSTRINGS.STRINGS_LABELS.poor   ? item.detalle.split(':')[1].split(',')[0] :  
                        alarmTable === CONSTSTRINGS.STRINGS_LABELS.RX     ? item.detalle.split("'")[1] : item.detalle
                    }}
                </template>            
                </EasyDataTable>
                <v-sheet color="transparent">
                    <GMapMap
                    v-if="showMapMarker"
                    :center="{lat: +30.968869, lng: -110.337264}"
                    :zoom="14"
                    map-type-id="satellite"
                    style="height: 700px"
                    >    

                        <!-- RAJANT MARKER --> 
                        <GMapMarker                         
                            :key="index"
                            v-for="(m, index) in markersrajant"
                            :position="m.position"
                            :icon="{
                                url: m.img,
                                scaledSize: m.size,
                                labelOrigin: m.tipo === CONSTSTRINGS.STRINGS_TYPES[7] || m.tipo === CONSTSTRINGS.STRINGS_TYPES[8] ? {x:25, y:40} : {x: 25, y:10},
                            }"
                            :label="{
                                text: m.label,
                                className: 'LabelRAJANT',
                                fontSize:'10px'
                            }"
                        >                            
                        </GMapMarker>

                        <!-- SM MARKER -->
                        <GMapMarker 
                        :key="index"
                        v-for="(m, index) in markerspmpsm"
                        :position="m.position"
                        :icon="{
                            url: require('@/assets/Images/server.png'),
                            scaledSize: {width: 10, height: 16},
                            labelOrigin: {x: 5, y:27},
                        }"
                        :title=m.ip
                        :label="{
                            text: m.name,
                            className: 'LabelPMPSM',
                            fontSize:'10px'
                        }"                        
                        >
                        </GMapMarker>

                    </GMapMap>
                </v-sheet>
            </v-sheet>
            <v-sheet class="text-center mt-6" color="transparent" v-else>
                <Prog_circular />
            </v-sheet>
        </v-sheet>
        <v-sheet v-if="notFoundItems" class="defaultCard" color=transparent>
            <h3> {{ t('reportPage.notFound')}} </h3>
        </v-sheet>
    </v-sheet>
</template>

<script setup lang="ts">
import axios from 'axios';
import { ref, onMounted, watch, computed, Ref } from 'vue';
import { useI18n }                              from 'vue-i18n';
import CONSTAPIS                                from '@/constants/apis';
import CONSTSTRINGS                             from '@/constants/strings';


// Components
import Prog_circular                            from '@/components/PrincipalComponents/Prog_circular.vue';

// Interfaces
import { ALLHEADER, HEADER }                from '@/interfaces/SmartLink/Tables';
import { ITEM_IP, ALARM_ITEM_IP,REPORT_ITEMS }  from '@/interfaces/SmartLink/Admin';
import { STRINGS }                              from '@/interfaces/SmartLink/General';
import { IMG_SIZE }                             from '@/interfaces/SmartLink/Imgs';
import { MARKERS }                              from '@/interfaces/SmartLink/Map';

const { t }             = useI18n();
const dateini           = ref('');
const datefin           = ref('');
const alarm             = ref('');
const alarmTable        = ref('');
const showTable         = ref(false);
const notFoundItems     = ref(false);
const selectAllIP       = ref(false);
const showMapMarker     = ref(false);
const disableBTN        = ref(true);
const itemsAlert        = ref([
    { title: computed(() => t('reportPage.alarm.rx')),         value: 'RX'     },
    { title: computed(() => t('reportPage.alarm.inter')),      value: 'Interf' },
    { title: computed(() => t('reportPage.alarm.poor')),       value: 'Poor'   },
]);
const ips:Ref<string[]>                     = ref([]);
const ipsAcceso:Ref<string[]>               = ref([]);
const ipsDistri:Ref<string[]>               = ref([]);
const ipsClient:Ref<string[]>               = ref([]);
const selectedIP:Ref<string[]>              = ref([]);
const itemsIP:Ref<ITEM_IP[]>                = ref([]);
const itemsIPClient:Ref<ITEM_IP[]>          = ref([]);
const itemsIPDistri:Ref<ITEM_IP[]>          = ref([]);
const itemsIPAcceso:Ref<ITEM_IP[]>          = ref([]);
const allItems:Ref<REPORT_ITEMS[]>          = ref([]);

const markersrajant:Ref<MARKERS[]>          = ref([]);
const markerspmpsm:Ref<MARKERS[]>           = ref([]);
const header:Ref<HEADER[]>               = ref([]);

const HEADERS_TABLE_KPI:ALLHEADER = {           
    header : [
        { text: computed(() => t('labels.cause')),      value: "causa",     sortable: false},
        { text: computed(() => t('labels.name')),       value: "name",      sortable: true },
        { text: computed(() => t('labels.info')),       value: "detalle",   sortable: true },
        { text: computed(() => t('labels.measure')),    value: "medida",    sortable: true },
        { text: computed(() => t('labels.ip')),         value: "ip",        sortable: true },
        { text: computed(() => t('labels.date')),       value: "fecha",     sortable: true },
        { text: computed(() => t('labels.latitude')),   value: "latitud",   sortable: true },
        { text: computed(() => t('labels.longitude')),  value: "longitud",  sortable: true },
    ],
    
};

/**
 * Constants
 */
    const EQUIPMENT_IMGS:STRINGS = {
    "PMP-AP"        : '',
    "PMP"           : '',
    'Solar Trailer' : '',
    'Tower'         : '',
    'Shovel'        : '',
    'Haultruck'     : '',
    'Driller'       : '',
    'Front Loader'  : '',
    'Watertruck'    : '',
}

const EQUIPMENT_SIZE:IMG_SIZE = {
    'Solar Trailer' : {width: 40, height: 60},
    'Tower'         : {width: 40, height: 60},
    'Shovel'        : {width: 50, height: 30},
    'Haultruck'     : {width: 40, height: 30},
    'Driller'       : {width: 40, height: 30},
    'Front Loader'  : {width: 40, height: 30},
    'Watertruck'    : {width: 40, height: 30},
}

// const showMap = () => {
//     showMapMarker.value = true;
// }

const getItems = () =>{
    
    allItems.value      = [];
    showTable.value     = true;
    disableBTN.value    = false;
    alarmTable.value    = alarm.value;
    showMapMarker.value = false;
    notFoundItems.value = false

    let params = {
        ip      : selectedIP.value,
        dateini : dateini.value,
        datefin : datefin.value,
        alarm   : alarm.value
    }

    axios.get(CONSTAPIS.APIS.report.alarmData,{params:params}).then(item =>{
        item.data.length == 0 ? showTable.value = false : null;
        item.data.length == 0 ? notFoundItems.value = true : notFoundItems.value = false;
        allItems.value      = item.data;
        markersrajant.value = [];
        markerspmpsm.value  = [];
        disableBTN.value    = true;
        header.value        = HEADERS_TABLE_KPI.header;
        allItems.value.forEach((element:REPORT_ITEMS) => {
            if(element.marca === CONSTSTRINGS.STRINGS_LABELS.Rajant){

                let label       =  '';
                let labelFragm  = element.name.split('-');

                element.tipo === CONSTSTRINGS.STRINGS_TYPES[7]    ? label = element.name                      : null;
                element.tipo === CONSTSTRINGS.STRINGS_TYPES[8]    ? label = labelFragm[0] + '-' + labelFragm[2]   : null;
                element.tipo === CONSTSTRINGS.STRINGS_TYPES[9]    ? label = labelFragm[2]                         : null;
                element.tipo === CONSTSTRINGS.STRINGS_TYPES[11]   ? label = labelFragm[0] + '-' + labelFragm[2]   : null;
                element.tipo === CONSTSTRINGS.STRINGS_TYPES[12]   ? label = labelFragm[0] + '-' + labelFragm[1]   : null;
                element.tipo === CONSTSTRINGS.STRINGS_TYPES[13]   ? label = labelFragm[0] + '-' + labelFragm[2]   : null;
                element.tipo === CONSTSTRINGS.STRINGS_TYPES[14]   ? label = labelFragm[0] + '-' + labelFragm[2]   : null;

                let lat = 0.00;
                let lng = 0.00;

                if(element.latitud !== null){
                    
                    let gradoLat    = parseFloat(element.latitud.slice(0,2));
                    let minuteLat   = (parseFloat(element.latitud.slice(2,-1))/60);
                    lat             = gradoLat + minuteLat;

                    let gradoLng    = parseFloat(element.longitud.slice(0,3));
                    let minuteLng   = (parseFloat(element.longitud.slice(3,-1))/60);
                    lng             = (gradoLng + minuteLng) * (-1);
                }
                markersrajant.value.push({
                    position    : { lat: parseFloat(lat.toFixed(6)), lng: parseFloat(lng.toFixed(6)) },
                    label       : label,
                    name        : element.name,
                    ip          : element.ip,
                    tipo        : element.tipo,
                    img         : EQUIPMENT_IMGS[element.tipo],
                    size        : EQUIPMENT_SIZE[element.tipo],
                })
            }else{
                let latitud     = parseFloat(element.latitud);
                let longitud    = parseFloat(element.longitud);
                markerspmpsm.value.push({
                    position    : { lat: latitud , lng: longitud },
                    label       : '',
                    name        : element.name,
                    ip          : element.ip,
                    tipo        : element.tipo,
                    img         : EQUIPMENT_IMGS[element.tipo],
                    size        : EQUIPMENT_SIZE[element.tipo],
                })
            }
        })
    });
}

/**
 * Hidden Report Tables and Maps
 */
watch(dateini,(p)=>{    
    if(p === null){
        showTable.value      = false;
    }
})

/**
 * Hidden Report Tables and Maps
 */
watch(datefin,(p)=>{    
    if(p === null){
        showTable.value      = false;
    }
})

/**
 * Hidden Report Tables and Maps
 */
watch(selectAllIP,(p)=>{    
    if(p){
        ips.value = [];
        itemsIP.value.forEach((item:ITEM_IP) => {
            ips.value.push(item.value)
        })
    }
    else{
        ips.value = [];
    }
})

/**
 * SELECT Alarm
 */
watch(alarm,()=>{
    ipsAcceso.value           = [];
    ipsDistri.value           = [];
    ipsClient.value           = [];
    selectedIP.value          = [];
})

watch(ipsAcceso,()=>{
    selectedIP.value =  ipsAcceso.value.concat(ipsClient.value,ipsDistri.value);          
})

watch(ipsClient,()=>{
    selectedIP.value =  ipsClient.value.concat(ipsAcceso.value,ipsDistri.value);        
})

watch(ipsDistri,()=>{
    selectedIP.value =  ipsDistri.value.concat(ipsAcceso.value,ipsClient.value);           
})

onMounted(() => {
    axios.get(CONSTAPIS.APIS.report.getIPreport).then(ip=>{
        ip.data.forEach((item:ALARM_ITEM_IP)=>{
            if(item.subtipo === CONSTSTRINGS.STRINGS_TYPES[10])  itemsIPClient.value.push({ title: item.hostname, value: item.ip });
            if(item.subtipo === CONSTSTRINGS.STRINGS_TYPES[2])   itemsIPDistri.value.push({ title: item.hostname, value: item.ip });
            if(item.subtipo === CONSTSTRINGS.STRINGS_TYPES[6])   itemsIPAcceso.value.push({ title: item.hostname, value: item.ip });
        })
        itemsIP.value =  itemsIPDistri.value;
    })
})
</script>

<style>
.arrow {
    border-top: 2px solid rgb(var(--v-theme-colorText)) !important;
    border-left: 2px solid rgb(var(--v-theme-colorText)) !important;
}

.v-selection-control .v-label{
    width: max-content !important;
}

</style>