<template>
    <v-sheet :style="'background-color:'+color+'!important'" color="bgCard" class="cardTopology" >       
        <v-col class="ma-0 pa-0" cols="12" align-self="center">
            <div class='text-center' v-if="!latency">    
                <v-chip                
                class="ma-2"
                :class="status === CONSTSTRINGS.STRINGS_TYPES[18] ? 'animationAlertChip' : status === CONSTSTRINGS.STRINGS_TYPES[17] ? 'animationAlarmChip' : 'normalChip'"
                :color="colorChip"           
                >
                <span class="textDashboard" :style="'color:'+colorChip">{{ status.toUpperCase() }}</span>
                </v-chip>                

                <div class="container-img">
                    <v-img class="img-topo" v-if="images !== null" :src="images"></v-img>
                </div>
                <div class='title-card'>
                    <span class="textDashboard"> {{ title }} </span>                
                    <div class='subtitle-card'>
                        {{ description }}
                    </div>
                </div>
                <div class='ip-card'>
                    {{ dirIP }}
                </div>
            </div>
            <div  class='text-center text-white'  v-else >
                <v-row class="ma-2" v-if="ping?.split(' ')[1] === 'ms'">  
                    <v-col cols="6" align-self="center" class="alineacion pa-0 pt-2">
                        <k-progress 
                            active  
                            :percent="100" 
                            :color="colorSliderAlignmet" 
                            bg-color="#d0d0d173"
                            type="lump" 
                            :cut-width="1" 
                            cut-color="#d0d0d173" 
                            :line-height="15"
                            :show-text="false"
                        >                                    
                        </k-progress>  
                        
                    </v-col>
                    <v-col class="pa-0 pl-2" cols='6' align-self="center">
                        {{ ping }}                                    
                    </v-col>
                </v-row>
                <v-row class="ma-2" v-else-if="ping?.split(' ')[2] === 'Disp.'">  
                    <v-col cols="5" class="text-end px-0 pr-1" >
                        {{ percentageDisp }}% 
                    </v-col>
                    <v-col class="pa-0" cols="7" align-self="center">
                        <v-progress-linear
                        :model-value="percentageBar"
                        height="15"
                        striped
                        :color="
                        percentageBar > 80 ? '#FF0000' : percentageBar <= 80 && percentageBar > 50 ? '#FFEE00' :'#00FF00'"
                        ></v-progress-linear>
                    </v-col>
                </v-row>
                <v-chip 
                v-else                
                class="ma-2"                
                color="white"           
                >
                {{ ping }}

                </v-chip>
                <div class="container-img">
                    <v-img class="img-topo" v-if="images !== null" :src="images"></v-img>
                </div>
                <div class='title-card'>
                    <span> {{ title }} </span>                
                    <div class='subtitle-card  text-white'>
                        {{ fecha }}
                    </div>
                </div>
                <div v-if="dirIP ==='ON' || dirIP === 'OFF'" >
                    <v-chip                
                    class="ma-2" 
                    variant="flat"                   
                    :color="dirIP === 'ON' ? 'green' : 'red'"           
                    >
                        {{ dirIP }}
                    </v-chip>  
                </div>
                <div v-else class='ip-card'>
                    {{ dirIP }}
                </div>
            </div>
        </v-col>
    </v-sheet>
</template>

<script setup lang="ts">
import { computed } from 'vue';
import CONSTSTRINGS    from '@/constants/strings';
import CONSTSCOLORS    from '@/constants/colors';
import CONSTANTIMGS from '@/constants/images';

// Interfaces
import { IMGS } from '@/interfaces/SmartLink/Imgs'

  const props = defineProps({
        status:{
            type:String,
            required:true,
        },
        title:{
            type:String,
            required:true,
        },
        description:{
            type:String,
            required:true,
        },
        dirIP:{
            type:String,
            required:true,
        },
        latency:{
            type:Boolean,
            value:false
        },
        ping:{
            type:String  
        },
        fecha:{
            type:String
        },
        color:{
            type:String,
            value:'transparent'
        }
    })
        //==================================================//
        //==================== VARIABLES ===================//
        //==================================================//
       
        const color                 = computed(() => props.status);
        const colorChip             = computed(() => CONSTSCOLORS.COLOR_CHIP[color.value.toUpperCase()]);

        const percentageBar         = computed(() => {
            let valor = 0;
            if(props.ping){
                valor = parseFloat(props.ping.split(' ')[0])
            }

            return valor;
        })
        const percentageDisp        = computed(() => {
            let valor = '0';
            if(props.ping){
                valor = props.ping.split(' ')[1]
            }

            return valor;
        })
        
        const CONST_IMG:IMGS        = {
            "PMP-SM"        : CONSTANTIMGS.IMAGES.pmp,
        };

        const images                = computed(() => CONST_IMG[props.description])



        /**
         * Computed
         * @returns Color String
         */
        const colorSliderAlignmet  = computed(()=>{
        let colorValue;
        let value           = props.ping?.split(' ')[1];
        let latencyValue    = props.ping?.split(' ')[0];
        if(value === 'ms' && latencyValue) {
            if(parseInt(latencyValue) < 0   || parseInt(latencyValue) >  500) colorValue = ['#EBE8E7','#E5E2E2'];
            if(parseInt(latencyValue) >= 0  && parseInt(latencyValue) <= 100) colorValue = ['#00FF00','#80FF00'];
            if(parseInt(latencyValue) > 100 && parseInt(latencyValue) <= 200) colorValue = ['#00FF00','#80FF00','#FFFF00'];
            if(parseInt(latencyValue) > 200 && parseInt(latencyValue) <= 500) colorValue = ['#00FF00','#80FF00','#FFFF00','#FBA977','#FF0000'];
        }            
            return colorValue
       })

</script>

<style scoped>

.cardTopology{    
  margin: 5px;
  border: 3px solid rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  padding: 5px;
  min-height: 150px;
  background-color: white;
  transition: 1s;
  transform: translateY(0px);
}

.cardTopology:hover{
    cursor: pointer;
    transform: translateY(-7px);
}

.container-img{
  height: 115px;
  margin: 0 auto;
 }

 .container-img .img-topo{
    filter: drop-shadow(0px 23px 12px rgba(63,63,63,0.5));
 }

.title-card{
  margin-top: 7px;
  height: 60px;
  overflow: hidden;
  line-height: 15px;
}

.title-card span{
  font-size: 14px;
  font-weight: 500;
}

.title-card .subtitle-card{
  font-size: 10px;
  font-family: 'Poppins', sans-serif;
  color: rgb(var(--v-theme-subtitle));
}

.ip-card{
  font-size: 12px;   
  font-family: 'Poppins', sans-serif;
  font-weight: 400;

}

.animationAlertChip{
    animation: appear 2s infinite ;
}

.animationAlarmChip{
    animation: appear 1s infinite ;
}

@keyframes appear{
    0%{
        opacity: 0;
    }
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}

</style>