// }

const getItems = () =>{
    
    allItems.value      = [];
    showTable.value     = true;
    disableBTN.value    = false;
    alarmTable.value    = alarm.value;
    showMapMarker.value = false;
    notFoundItems.value = false

    let params = {
        ip      : selectedIP.value,
        dateini : dateini.value,
        datefin : datefin.value,
        alarm   : alarm.value
    }

    axios.get(CONSTAPIS.APIS.report.alarmData,{params:params}).then(item =>{
        item.data.length == 0 ? showTable.value = false : null;
        item.data.length == 0 ? notFoundItems.value = true : notFoundItems.value = false;
        allItems.value      = item.data;
        markersrajant.value = [];
        markerspmpsm.value  = [];
        disableBTN.value    = true;
        header.value        = HEADERS_TABLE_KPI.header;
        allItems.value.forEach((element:REPORT_ITEMS) => {
            if(element.marca === CONSTSTRINGS.STRINGS_LABELS.Rajant){

                let label       =  '';
                let labelFragm  = element.name.split('-');

                element.tipo === CONSTSTRINGS.STRINGS_TYPES[7]    ? label = element.name                      : null;
                element.tipo === CONSTSTRINGS.STRINGS_TYPES[8]    ? label = labelFragm[0] + '-' + labelFragm[2]   : null;
                element.tipo === CONSTSTRINGS.STRINGS_TYPES[9]    ? label = labelFragm[2]                         : null;
                element.tipo === CONSTSTRINGS.STRINGS_TYPES[11]   ? label = labelFragm[0] + '-' + labelFragm[2]   : null;
                element.tipo === CONSTSTRINGS.STRINGS_TYPES[12]   ? label = labelFragm[0] + '-' + labelFragm[1]   : null;
                element.tipo === CONSTSTRINGS.STRINGS_TYPES[13]   ? label = labelFragm[0] + '-' + labelFragm[2]   : null;
                element.tipo === CONSTSTRINGS.STRINGS_TYPES[14]   ? label = labelFragm[0] + '-' + labelFragm[2]   : null;

                let lat = 0.00;
                let lng = 0.00;

                if(element.latitud !== null){
                    
                    let gradoLat    = parseFloat(element.latitud.slice(0,2));
                    let minuteLat   = (parseFloat(element.latitud.slice(2,-1))/60);
                    lat             = gradoLat + minuteLat;

                    let gradoLng    = parseFloat(element.longitud.slice(0,3));
                    let minuteLng   = (parseFloat(element.longitud.slice(3,-1))/60);
                    lng             = (gradoLng + minuteLng) * (-1);
                }
                markersrajant.value.push({
                    position    : { lat: parseFloat(lat.toFixed(6)), lng: parseFloat(lng.toFixed(6)) },
                    label       : label,
                    name        : element.name,
                    ip          : element.ip,
                    tipo        : element.tipo,
                    img         : EQUIPMENT_IMGS[element.tipo],
                    size        : EQUIPMENT_SIZE[element.tipo],
                })
            }else{
                let latitud     = parseFloat(element.latitud);
                let longitud    = parseFloat(element.longitud);
                markerspmpsm.value.push({
                    position    : { lat: latitud , lng: longitud },
                    label       : '',
                    name        : element.name,
                    ip          : element.ip,
                    tipo        : element.tipo,
                    img         : EQUIPMENT_IMGS[element.tipo],
                    size        : EQUIPMENT_SIZE[element.tipo],
                })
            }
        })
    });
}

/**
 * Hidden Report Tables and Maps
 */
watch(dateini,(p)=>{    
    if(p === null){
        showTable.value      = false;
    }
})

/**
 * Hidden Report Tables and Maps
 */
watch(datefin,(p)=>{    
    if(p === null){
        showTable.value      = false;
    }
})

/**
 * Hidden Report Tables and Maps
 */
watch(selectAllIP,(p)=>{    
    if(p){
        ips.value = [];
        itemsIP.value.forEach((item:ITEM_IP) => {
            ips.value.push(item.value)
        })
    }
    else{
        ips.value = [];
    }
})

/**
 * SELECT Alarm
 */
watch(alarm,()=>{
    ipsAcceso.value           = [];
    ipsDistri.value           = [];
    ipsClient.value           = [];
    selectedIP.value          = [];
})

watch(ipsAcceso,()=>{
    selectedIP.value =  ipsAcceso.value.concat(ipsClient.value,ipsDistri.value);          
})

watch(ipsClient,()=>{
    selectedIP.value =  ipsClient.value.concat(ipsAcceso.value,ipsDistri.value);        
})

watch(ipsDistri,()=>{
    selectedIP.value =  ipsDistri.value.concat(ipsAcceso.value,ipsClient.value);           
})

onMounted(() => {
    axios.get(CONSTAPIS.APIS.report.getIPreport).then(ip=>{
        ip.data.forEach((item:ALARM_ITEM_IP)=>{
            if(item.subtipo === CONSTSTRINGS.STRINGS_TYPES[10])  itemsIPClient.value.push({ title: item.hostname, value: item.ip });
            if(item.subtipo === CONSTSTRINGS.STRINGS_TYPES[2])   itemsIPDistri.value.push({ title: item.hostname, value: item.ip });
            if(item.subtipo === CONSTSTRINGS.STRINGS_TYPES[6])   itemsIPAcceso.value.push({ title: item.hostname, value: item.ip });
        })
        itemsIP.value =  itemsIPDistri.value;
    })
})

