<template>
    <v-sheet class="defaultCard" color="bgtopology">
        <v-row justify="space-between" class="ma-0 mb-4">

            <v-col class="pa-0" cols="12" sm="4" md="7" lg="7" xl='8' align-self="center" >                
                <span  class="titlePages">{{ t('sidebar.kpi.lastConect') }}</span>
            </v-col>
             
            <!-- SEARCH FIELD -->
            <v-col class="pa-0" cols="12" sm="8" md="5" lg="5" xl='4' style="text-align: -webkit-right;" >
                <v-text-field
                    v-if="items.length !== 0"
                    :style="!smAndDown ? 'max-width: 400px !important; margin-top: 20px':' margin-top: 10px'"             
                    v-model="searchValue"
                    :label="t('search')"
                    variant="outlined"
                    density="compact" 
                    hide-details 
                ></v-text-field>
            </v-col>

        </v-row>

        <!-- TABLE COMPONENT -->
        <EasyDataTable
        v-if="items.length !== 0"
        table-class-name="customize-table"
        :search-field="searchField"
        :search-value="searchValue"
        :headers="headers"
        :items="items"
        :class="'pt-10'"
        show-index
        >
         <template #item-timeDown ="item">
           {{ item.timeDown }} min
        </template>
        <template #item-dateDown ="item">
            {{ item.dateDown === '' ? '+ 1 dia sin conexin' : item.dateDown }}
        </template>
        </EasyDataTable>

        <v-row class="ma-0">
            <v-text-field
            :style="!smAndDown ? 'max-width: 400px !important; margin-top: 20px':' margin-top: 10px'"             
            v-model="email"
            :label="t('mail')"
            variant="outlined"
            density="compact" 
            hide-details 
            ></v-text-field>
            <v-btn 
            :class="'ma-2 mt-6'" 
            color="success"
            :disabled="email==''"
            @click='sendMail()'
            > 
                {{ t('buttons.send') }} 
            </v-btn>
        </v-row>

    </v-sheet>
</template>

<script setup lang="ts">

import { computed, onMounted, ref, Ref,/** ComputedRef, watch, */  } from 'vue'
import axios                            from 'axios';
import { useStore }                     from 'vuex';
import { useDisplay }                   from 'vuetify';
import { useI18n }                      from 'vue-i18n';

// CONSTANTS
import CONSTAPIS                        from '@/constants/apis';
// import CONSTSTRINGS                     from '@/constants/strings';
import { ALLHEADER, HEADER }        from '@/interfaces/SmartLink/Tables';
import { GENERAL_EQUIPAMENT, LAST_CONECTION }        from '@/interfaces/SmartLink/Equips';
// import { STRINGS, SNR}                  from '@/interfaces/SmartLink/KPI';


const { t }                         = useI18n();
const store                         = useStore();
const { smAndDown }                 = useDisplay();

const searchField                   = ref();
const searchValue                   = ref("");
const headers:Ref<HEADER[]>         = ref([]);
const items:Ref<LAST_CONECTION[]>   = ref([]);
const countDownBYIPs:Ref<GENERAL_EQUIPAMENT[]>       = ref([]);
const ipArrayList: Ref<string[]>    = ref([]);
const email                         = ref('');

const HEADERS_TABLE_KPI:ALLHEADER  = {
    latency     : [
        // { text: computed(() => t('kpiPage.headers.general.time')),              value: "fecha",                     sortable: false},
        { text: "IP",                                                           value: "ip",                        sortable: true },
        { text: computed(() => t('kpiPage.headers.general.hostname')),          value: "name",                  sortable: true },
        { text: "Tiempo sin Comunicacion",              value: "timeDown",                      sortable: true },
        { text: "Hora de Inicio",           value: "dateDown",                   sortable: true },
        // { text: "Cantidad de caidas (24h)",           value: "timesDown",                   sortable: true },
    ]
};

const getDataTable = async () =>{
    items.value         = [];
    headers.value       = HEADERS_TABLE_KPI.latency;

    await axios.get(CONSTAPIS.APIS.kpi.getLastConectionHaultruck).then(async (response) =>{ 
        
        let finalItems = response.data;
        let minutes = 0;
        let fecha = '';
        let previousStatus = 'DOWN';
        let previousFecha  = '';
        let countDown = 0;
        ipArrayList.value.forEach((uniqIP:string) => {
            minutes = 0;
            fecha = '';
            countDown = 0;
            previousFecha = '';
            previousStatus = 'DOWN'

            let objConection  = {
                ip          : uniqIP,
                name        : '',
                timeDown    : 0,
                timesDown   : 0,
                dateDown    : ''
            }

            finalItems.forEach((item:GENERAL_EQUIPAMENT) => {
                
                if(item.ip === uniqIP) {
                    objConection.name = item.name??'';
                    let finalLatencia = item.latencia??0
                    let getStatus = finalLatencia >=0 && finalLatencia < 100 ? 'ok' : 
                                    finalLatencia >= 100 && finalLatencia < 200 ? 'alert' :
                                    finalLatencia >= 200 && finalLatencia < 500 ? 'alarm' : 'down' 

                    if(getStatus.toUpperCase() === 'DOWN'){
                        minutes = minutes + 15;
                        countDown = countDown + 1;
                        
                        if(previousStatus !== 'DOWN'){
                            fecha = item.conection??'';
                        }else{
                            fecha = previousFecha;
                        }
                        previousStatus = 'DOWN';
                    }
                    else{
                        previousStatus = 'OTHER';
                        previousFecha = item.conection??'';
                        fecha = previousFecha;
                        minutes = 0;
                    }
                }
                
            })
            objConection.timeDown   = minutes;
            objConection.timesDown  = countDown;
            objConection.dateDown   = fecha;

            if(minutes > 15) {
                items.value.push(objConection)
            }
        })
    })
    
}

const getCountDownByIp =  async() => {
    await axios.get(CONSTAPIS.APIS.kpi.getCountLastConectionHaultruck).then((response) =>{
        countDownBYIPs.value = response.data;
        countDownBYIPs.value.forEach((item:GENERAL_EQUIPAMENT) => {
            ipArrayList.value.push(item.ip??'')
        })
     })
}


const sendMail = () => {

    let params = {
        mail    : items.value,
        toMail  : email.value 
    }

    axios.post(CONSTAPIS.APIS.kpi.sendMail,params).then((response) =>{
        // console.log(response)
        let sendStatus = response.data.body;
        if(sendStatus === 'ENVIADO'){
            store.dispatch('showSnackBar', {type: 'setSnackAdd', msg: 'Enviado con Exito'});
            email.value = ''
        }
    })
}

onMounted(async() => {
    await getCountDownByIp()
    await getDataTable()
})

</script>
