<template>
    <v-row class="mx-1 mt-8">
        <v-col v-if="showTable" cols="12" align-self="center" >
            <v-sheet :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="mb-4 text-center card-container-table" color="bgtopology">
                <h4 class="py-3"> {{ t('title.tableTitle') }} </h4>
                <v-sheet v-if="arrayTable.length !== 0" height="354" color="transparent" class="pt-10">
                    <BasicTableVue  :items="arrayTable" :hidden="true"/>
                </v-sheet>
                <v-sheet v-else class="text-center" color="transparent">
                    <Prog_circular />
                </v-sheet>
            </v-sheet>            
        </v-col>
    </v-row>
</template>

<script setup lang="ts">
import { onMounted, ref, Ref } from 'vue';
import axios            from 'axios';
import { useDisplay }   from 'vuetify';
import { useI18n }      from 'vue-i18n';

// COMPONENTS
import Prog_circular    from '@/components/PrincipalComponents/Prog_circular.vue';
import BasicTableVue    from '@/components/SmartLink/tables/BasicTable.vue';

// CONSTANTS 
import CONSTAPIS        from '@/constants/apis';

// INTERFACES
import { PMPAP } from '@/interfaces/SmartLink/Graphs'

const props = defineProps({
    title   : { required:true },
    ip      : { required:true, type:String },
})

//==================================================//
//==================== VARIABLES ===================//
//==================================================//

/**
 * Vue / Vuetify
 */
const { xs }                        = useDisplay();
const { t }                        = useI18n();

/**
 * Component
 */
const arrayTable:Ref<PMPAP[]>       = ref([]);
const showTable                     = ref(true);

//==================================================//
//===================== METHODS ====================//
//==================================================//

const params = {
    ip      : props.ip,
    type    : CONSTAPIS.APIS_STRINGS.latency
}

/**
 * get All Data from API to Table
 */
const getDataTable = () => {
    axios.get(CONSTAPIS.APIS.topology.getTableTopo,{params:params}).then((item) => {
        if(item.data.length === 0) {
            showTable.value     = false;
        }else{
            showTable.value     = true;
            let prevArray = item.data;
            let prevObj:PMPAP[]   = [];
            prevArray.forEach((item:PMPAP) => {
                let finalObj = {
                    fecha   : item.fecha,
                    ip      : item.ip,
                    status  : item.status,
                    causa   : 'Poor Signal',
                    detalle : `Latencia: ${item.latencia}`
                }
                prevObj.push(finalObj)
            })
            arrayTable.value    =  prevObj;                
        }
    })
}

onMounted(() => {
    getDataTable();
})

</script>

<style>

.card-container-table{
    height: 100% !important;
    min-height: 100% !important;
}

 .card-dialogs-graph{
    padding: 20px;
    border-radius: 10px;
 }

 .card-dialogs-graph-wop{
    padding:5px;
    border-radius: 10px;
 }

 .highcharts-root{
    border-radius: 20px;
 }

 .vue3-easy-data-table__footer{
    background-color: transparent !important;
    color: rgb(var(--v-theme-colorText)) !important;
    border-top: none !important;
 }

 .easy-data-table__rows-selector ul.select-items li{
    color: black ;
 }

 .easy-data-table__rows-selector .rows-input__wrapper{
    border-bottom: 1px solid rgb(var(--v-theme-colorText)) !important;
 }

 .easy-data-table__rows-selector .rows-input__wrapper .triangle{
    border-top: solid 6px rgb(var(--v-theme-colorText)) !important;
 }
</style>