<template>
<div v-if="showTotalItems">
    <v-sheet v-for="(item,i) in finalItemsKPI" :key="i" color="bgtopology" class="defaultCard" >
        <span class="ml-4 titleDashboard">{{ t(`labels.${item.valor}`) }}</span>
        <v-row class="ma-0" :justify="mdAndDown ? 'center' : 'space-between'">
            <v-sheet color="transparent" :width="smAndDown ? 500 : 395" height="460">
                <v-col class="pa-0" style="height: 460px; align-content: center;">                    
                    <GaugeComponent
                    :recharge="false"
                    :array-items="item.total"
                    :valorCard ="item.valor"
                    />
                </v-col>
            </v-sheet>
            <v-sheet color='transparent' :style="smAndDown ?'width: 100%' :`width: calc(100% - ${width}px)`">
                <TimeLineStatus 
                :array-items="item.total"
                />
            </v-sheet>                    
        </v-row>

        <v-row class='ma-0 ml-2' justify="center" >
            <v-sheet color="transparent" :width=" width" :class="mdAndDown ? 'pb-3' : 'pr-2'">
                <div class="pl-2">
                    {{ t('labels.msn') }}
                </div>
                <TimeLineAlerts
                v-if="item.alarm.length > 0"
                :array-items="item.alarm"
                :width="400"
                :height="height"
                />
                <p class="mt-8" v-else>
                    <span class="textDashboard" > {{ t('noAlertsAlarm') }} </span>  
                </p>
            </v-sheet>

            <v-sheet color=transparent :style="mdAndDown ?'width: 100%' :`width: calc(100% - ${width}px)`" class="mt-2">
                <v-row class="ma-0 mt-4">
                    <v-col cols="12" sm="6" md="3" :class="mdAndDown ? 'pa-2' :'pa-0 pr-2'">
                        <SingleCard class="cardSingleKPI" @click="goToTopology(item.valor)" :heigth="height" :bottomText="t('labels.ok_pl')" :icon="'mdi-check'" :color-icon="CONSTCOLORS.COLOR_HTML.green" :middleText="item.count.ok.toString()"/>
                    </v-col>
                    <v-col cols="12" sm="6" md="3" :class="mdAndDown ? 'pa-2' :'pa-0 px-2'">
                        <SingleCard class="cardSingleKPI" @click="goToTopology(item.valor)" :heigth="height" :bottomText="t('labels.alarm_pl')" :icon="'mdi-lightbulb-on-outline'" :color-icon="CONSTCOLORS.COLOR_HTML.yellow" :middleText="item.count.alarm.toString()"/>
                    </v-col>
                    <v-col cols="12" sm="6" md="3" :class="mdAndDown ? 'pa-2' :'pa-0 px-2'">
                        <SingleCard class="cardSingleKPI" @click="goToTopology(item.valor)" :heigth="height" :bottomText="t('labels.alert_pl')" :icon="'mdi-exclamation-thick'" :color-icon="CONSTCOLORS.COLOR_HTML.red" :middleText="item.count.alert.toString()"/>
                    </v-col>
                    <v-col cols="12" sm="6" md="3" :class="mdAndDown ? 'pa-2' :'pa-0 px-2'">
                        <SingleCard class="cardSingleKPI" @click="goToTopology(item.valor)" :heigth="height" :bottomText="t('labels.down_pl')" :icon="'mdi-close'" :color-icon="CONSTCOLORS.COLOR_HTML.grey" :middleText="item.count.down.toString()"/>
                    </v-col>
                </v-row>
            </v-sheet>
        </v-row>

    </v-sheet>
</div>

<v-sheet v-else color="bgtopology" class="defaultCard text-center">
    <Prog_circular />
</v-sheet>

</template>

<script setup lang="ts">
import {  Ref, ref, computed, watch, onMounted, ComputedRef } from 'vue';
import router               from '@/router';
import { useI18n }          from 'vue-i18n';
import { useDisplay }       from 'vuetify';
import axios                from 'axios';
import { useStore }         from 'vuex';
import { PropType }         from 'vue';

// CONSTANTS
import CONSTAPIS            from '@/constants/apis';
import CONSTSTRINGS         from '@/constants/strings';
import CONSTCOLORS          from '@/constants/colors';

// COMPONENTS
import GaugeComponent       from '@/components/SmartLink/dashboard/GaugeComponent.vue';
import TimeLineStatus       from '@/components/SmartLink/dashboard/TimeLineStatus.vue';
import TimeLineAlerts       from '@/components/SmartLink/dashboard/TimeLineAlerts.vue';
import Prog_circular        from '@/components/PrincipalComponents/Prog_circular.vue';
import SingleCard           from '@/components/SmartLink/topology/SingleCard.vue';

// import {OPERABILITY}            from '@/constants/test'

// INTERFACES
import { ALARMTYPE }        from '@/interfaces/SmartLink/KPI';
import { FinalStatus, SystemStatus } from '@/interfaces/SmartLink/Equips';

const { t } = useI18n();

const props = defineProps({
    recharge            : { type: Boolean, required: true },
    countEquipStatus    : { type: Object as PropType<SystemStatus>, required: true },
})

const { mdAndDown, smAndDown }  = useDisplay();
const isRecharge                = computed(()=> props.recharge)
const totalItems:Ref<string[]>  = ref([]);
const width             = ref('425');
const height            = ref(160);

watch(isRecharge,(rech:boolean) => {
    if(rech){ totalItems.value = [] }
})

const alarmArrayDist:Ref<ALARMTYPE[]>   = ref([]);
const alarmArrayAcce:Ref<ALARMTYPE[]>   = ref([]);
const alarmArrayClie:Ref<ALARMTYPE[]>   = ref([]);
const totalArrayDist:Ref<FinalStatus[]> = ref([]);
const totalArrayAcce:Ref<FinalStatus[]> = ref([]);
const totalArrayClie:Ref<FinalStatus[]> = ref([]);
const finalCount:ComputedRef<SystemStatus> = computed(() => props.countEquipStatus);
const finalItemsKPI:Ref<any> = ref({
    distri: { valor:CONSTSTRINGS.STRING_GENERAL.distri, alarm: alarmArrayDist.value, total: totalArrayDist.value, count: finalCount.value.distri, showError: false },
    acceso: { valor:CONSTSTRINGS.STRING_GENERAL.access, alarm: alarmArrayAcce.value, total: totalArrayAcce.value, count: finalCount.value.acceso, showError: false },
    client: { valor:CONSTSTRINGS.STRING_GENERAL.client, alarm: alarmArrayClie.value, total: totalArrayClie.value, count: finalCount.value.client, showError: false },
})

const showTotalItems                    = ref(false);
const totalItemsDIST:Ref<FinalStatus[]> = ref([]);
const totalItemsACCE:Ref<FinalStatus[]> = ref([]);
const totalItemsCLIE:Ref<FinalStatus[]> = ref([]);

// const finalOpe = OPERABILITY;
const getAllOperabilityData = async() => {
    await axios.get(CONSTAPIS.APIS.dashboard.getOperability).then((allitems) => {
        let finalItemOpe = allitems.data;
        // let finalItemOpe = finalOpe;
        finalItemOpe.forEach((item:FinalStatus)=>{
            if(item.subtipo === CONSTSTRINGS.STRINGS_TYPES[2])  totalItemsDIST.value.push(item)
            if(item.subtipo === CONSTSTRINGS.STRINGS_TYPES[6])  totalItemsACCE.value.push(item)
            if(item.subtipo === CONSTSTRINGS.STRINGS_TYPES[10]) totalItemsCLIE.value.push(item)
        })
    })
}

const alarmItemsDIST:Ref<ALARMTYPE[]>   = ref([]);
const alarmItemsACCE:Ref<ALARMTYPE[]>   = ref([]);
const alarmItemsCLIE:Ref<ALARMTYPE[]>   = ref([]);
const showTLstatusDIST                  = ref(false);
const showTLstatusACCE                  = ref(false);
const showTLstatusCLIE                  = ref(false);

const getAlarmToDashBoard = async () => {
    alarmItemsDIST.value = [];
    alarmItemsACCE.value = [];
    alarmItemsCLIE.value = [];
    await axios.get(CONSTAPIS.APIS.dashboard.alarmtodash).then((allitems) => {
        let finalItemAlarm = allitems.data;
        finalItemAlarm.forEach((item:ALARMTYPE)=>{
            if(item.subtipo === CONSTSTRINGS.STRINGS_TYPES[2])  alarmItemsDIST.value.push(item)
            if(item.subtipo === CONSTSTRINGS.STRINGS_TYPES[6])  alarmItemsACCE.value.push(item)
            if(item.subtipo === CONSTSTRINGS.STRINGS_TYPES[10]) alarmItemsCLIE.value.push(item)
        })
        alarmItemsDIST.value.length > 0 ?  showTLstatusDIST.value = true :  showTLstatusDIST.value = false;
        alarmItemsACCE.value.length > 0 ?  showTLstatusACCE.value = true :  showTLstatusACCE.value = false;
        alarmItemsCLIE.value.length > 0 ?  showTLstatusCLIE.value = true :  showTLstatusCLIE.value = false;
    })
}

const matchItems = async () => {
    finalItemsKPI.value.distri.total        = totalItemsDIST.value;
    finalItemsKPI.value.distri.alarm        = alarmItemsDIST.value;
    finalItemsKPI.value.distri.showError    = showTLstatusDIST.value;
    finalItemsKPI.value.acceso.total        = totalItemsACCE.value;
    finalItemsKPI.value.acceso.alarm        = alarmItemsACCE.value;
    finalItemsKPI.value.acceso.showError    = showTLstatusACCE.value;
    finalItemsKPI.value.client.total        = totalItemsCLIE.value;
    finalItemsKPI.value.client.alarm        = alarmItemsCLIE.value;
    finalItemsKPI.value.client.showError    = showTLstatusCLIE.value;
    showTotalItems.value                    = true;
}

const goToTopology = (viewPage:string) => {
    router.push(`/smartlink/topology#${viewPage}`)
}

const store     = useStore();

onMounted(async () => {
    getAlarmToDashBoard();
    await getAllOperabilityData();
    await matchItems();
    store.commit('setTotalKPI',finalItemsKPI.value)
})

</script>