<template>
    <div v-if="showChart">
        <Chart :constructorType="'stockChart'" class="hc" :options="finalOptions" ref="chart"></Chart>
    </div>
</template>

<script setup lang="ts">
import { ref, Ref, onMounted }    from 'vue';
import { Chart }            from 'highcharts-vue';
import { useI18n }          from 'vue-i18n';
import { getOptionsInGraphs } from '@/functions/project';

const props = defineProps({
    arrayItems: { required:true, type: Array }, arrayNames: { required:true, type: Array},
    title: { required:true, type:String }
})

const { t }                 = useI18n();
const finalARRAYITEMS       = ref(props.arrayItems);
const finalARRAYNAMES       = ref(props.arrayNames);
const finalObjs:Ref<any[]>         = ref([]);
const showChart = ref(false)

const rangeSelectoValue = [t('labels.day'),t('labels.week'),t('labels.month')];
const seriesValue = ref(finalObjs.value)
/* eslint-disable */

/**
 * getOptionsInGraphs(tipo, labels de las fechas, serie, titulo, subtitulo, ocultar leyenda, altura, tooltip, yaxis)
 */
const finalOptions:any = ref(getOptionsInGraphs('line',rangeSelectoValue,seriesValue,props.title,'',true,550,{valueDecimals: 2}, {}));

const getAllData = async() => {
    for(let i=0; i<finalARRAYITEMS.value.length; i++ ){
        finalObjs.value.push(
            {
                name: finalARRAYNAMES.value[i],
                data: finalARRAYITEMS.value[i]
            },
        )
    }
}


onMounted(async () => {
    await getAllData();
    showChart.value = true;
})
</script>