<template>
    <v-row class="ma-0">
        <v-col cols="12" class="pa-0">
            <v-sheet v-if="arrayTable.length > 0" class="defaultCard" color="bgtopology">
                <EasyDataTable
                table-class-name="customize-table"
                show-index
                :headers="HEADERS"
                :items="arrayTable"
                :rows-per-page="6"
                >         
                </EasyDataTable>
            </v-sheet>                
        </v-col>

        <v-col cols="12" class="pa-0"> 
            <v-sheet v-if="allData.length > 0" class="defaultCard" color="bgtopology">
                <MultipleChart 
                :arrayItems="finalObjGRAPHS.ifIn.arrayItems"
                :arrayNames="finalObjGRAPHS.ifIn.arrayNames"   
                title="InUCast"
                />
            </v-sheet>
            <v-sheet v-else class="text-center defaultCard" color="bgtopology">
                <Prog_circular />
            </v-sheet>
        </v-col>

        <v-col cols="12" class="pa-0"> 
            <v-sheet v-if="allData.length > 0" class="defaultCard" color="bgtopology">
                <MultipleChart
                :arrayItems="finalObjGRAPHS.ifOut.arrayItems"
                :arrayNames="finalObjGRAPHS.ifOut.arrayNames"    
                title="OutUCast"
                />
            </v-sheet>
            <v-sheet v-else class="text-center defaultCard" color="bgtopology">
                <Prog_circular />
            </v-sheet>
        </v-col>
        
        <v-col cols="12" class="pa-0"> 
            <v-sheet v-if="allData.length > 0" class="defaultCard" color="bgtopology">
                <MultipleChart
                    :arrayItems="finalObjGRAPHS.ifSpeed.arrayItems"
                    :arrayNames="finalObjGRAPHS.ifSpeed.arrayNames"
                    title="Speed"
                />
            </v-sheet>
            <v-sheet v-else class="text-center defaultCard" color="bgtopology">
                <Prog_circular />
            </v-sheet>
        </v-col>


        <!-- LTE -->
        <v-col cols="12" v-if="type === CONSTSTRINGS.STRINGS_HTML.LTE" class="pa-0"> 
            <v-sheet v-if="allData.length > 0" class="defaultCard" color="bgtopology">
                <MultipleChart
                    :arrayItems="finalObjGRAPHS.RXBytes.arrayItems"
                    :arrayNames="finalObjGRAPHS.RXBytes.arrayNames"
                    title="RXBytes"
                />
            </v-sheet>
            <v-sheet v-else class="text-center defaultCard" color="bgtopology">
                <Prog_circular />
            </v-sheet>
        </v-col>

        <v-col cols="12" v-if="type === CONSTSTRINGS.STRINGS_HTML.LTE" class="pa-0"> 
            <v-sheet v-if="allData.length > 0" class="defaultCard" color="bgtopology">
                <MultipleChart
                    :arrayItems="finalObjGRAPHS.TXBytes.arrayItems"
                    :arrayNames="finalObjGRAPHS.TXBytes.arrayNames"
                    title="TXBytes"
                />
            </v-sheet>
            <v-sheet v-else class="text-center defaultCard" color="bgtopology">
                <Prog_circular />
            </v-sheet>
        </v-col>
        
        <v-col cols="12" class="pa-0" v-for="(item, i) in finalObjESTADO"  :key='i'>
            <v-sheet v-if="item.showItems"  class="defaultCard" color="bgtopology">
                <SingleChart   :name="item.name" :first-array="item.arrayItems" :title="item.title" :color="item.color"/>                    
            </v-sheet>
        </v-col>
    </v-row>
</template>

<script setup lang="ts">
import { onMounted, ref, Ref } from 'vue';
import axios                from 'axios';

//CONSTANTS
import CONSTSTRINGS         from '@/constants/strings';
import CONSTCOLORS          from '@/constants/colors';
import CONSTAPIS            from '@/constants/apis';

// COMPONENTS   
import Prog_circular        from '@/components/PrincipalComponents/Prog_circular.vue';
import SingleChart          from '@/components/SmartLink/charts/SingleChart.vue';
import MultipleChart        from '@/components/SmartLink/charts/MultipleCharts.vue';

// INTERFACES 
import { HEADER }               from '@/interfaces/SmartLink/Tables';
import { LTE_GRAPH, LTE_TABLE } from '@/interfaces/SmartLink/LTE'
import { ENLACES } from '@/interfaces/GestorHCGLINK/Equips';

const props = defineProps({
    ip          : { required:true, type:String },
    type        : { required:true, type:String },
    arrayTable  : { type:Array, required:true },
})

const allData:Ref<LTE_GRAPH[]>          = ref([]);
const rssi:Ref<number[][]>              = ref([]);
const rsrp:Ref<number[][]>              = ref([]);
const rsrq:Ref<number[][]>              = ref([]);

const finalObjGRAPHS:Ref<any>           = ref({});
const finalObjESTADO:Ref<any>           = ref([]);
const showInfoModem                     = ref(false);

const HEADERS:HEADER[] = [
        { text: 'Nombre',           value: "descrip",           sortable: true },
        { text: 'Fecha',            value: "fecha",             sortable: true },
        { text: "InUCast (GPkts)",  value: "ifInUcastPkts",     sortable: true },
        { text: "MTU (Bytes)",      value: "ifMtu",             sortable: true },
        { text: "OutUCast (GPkts)", value: "ifOutUcastPkts",    sortable: true },
        { text: "Speed (Gbps)",     value: "ifSpeed",           sortable: true },
    ]

const getLTEDataGraph = () =>{        
    allData.value = [];
    axios.get(CONSTAPIS.APIS.lte.getAllGraphData,{params:{ip:props.ip}}).then((item) => {

        allData.value = item.data;
        finalObjGRAPHS.value = {};
        let previousArray = item.data;

        if(previousArray.length > 0) {
            let previousObject:any = {};
            let finalOBject: any = {
                ifIn    : {arrayNames:[], arrayItems:[]},
                ifOut   : {arrayNames:[], arrayItems:[]},
                ifSpeed : {arrayNames:[], arrayItems:[]},
                TXBytes : {arrayNames:[], arrayItems:[]},
                RXBytes : {arrayNames:[], arrayItems:[]},
            };
            let arrayKeyObj:string[] = [];

            let previousObjectEstado:any = [];
            let arrayRSSI:number[][] = [];
            let arrayRSRQ:number[][] = [];
            let arrayRSRP:number[][] = [];
            
            previousArray.forEach((element:ENLACES)=>{
                let time            = new Date(element.fecha);
                let milliseconds    = time.getTime();

                let finalEnlaces = JSON.parse(element.enlaces);

                finalEnlaces.forEach((enlace:any) => {
                    let name = enlace.ifDescr.split("\"")[1];

                    if(!previousObject[name]){
                        previousObject[name] = {}
                        arrayKeyObj.push(name)
                    }

                    if(!previousObject[name]['ifInUcastPkts']){
                        previousObject[name]['ifInUcastPkts'] = []
                    }
                    if(!previousObject[name]['ifOutUcastPkts']){
                        previousObject[name]['ifOutUcastPkts'] = []
                    }
                    if(!previousObject[name]['ifSpeed']){
                        previousObject[name]['ifSpeed'] = []
                    }
                    if(!previousObject[name]['TXBytes']){
                        previousObject[name]['TXBytes'] = []
                    }
                    if(!previousObject[name]['RXBytes']){
                        previousObject[name]['RXBytes'] = []
                    }
                    previousObject[name]['ifInUcastPkts'].push([milliseconds,parseInt(enlace.ifInUcastPkts)])
                    previousObject[name]['ifOutUcastPkts'].push([milliseconds,parseInt(enlace.ifOutUcastPkts)])
                    previousObject[name]['ifSpeed'].push([milliseconds,parseInt(enlace.ifSpeed)])
                    previousObject[name]['TXBytes'].push([milliseconds,parseInt(enlace.TXBytes)])
                    previousObject[name]['RXBytes'].push([milliseconds,parseInt(enlace.RXBytes)])
                })


                let finalEstado  = JSON.parse(element.estado);

                if(finalEstado.status === 'connected') {
                    let rssi = parseInt(finalEstado.rssi.split('d')[0])
                    let rsrq = parseInt(finalEstado.rsrq.split('d')[0])
                    let rsrp = parseInt(finalEstado.rsrp.split('d')[0])
                    arrayRSSI.push([milliseconds,rssi])
                    arrayRSRQ.push([milliseconds,rsrq])
                    arrayRSRP.push([milliseconds,rsrp])
                }
                

            })
            arrayKeyObj.forEach((item:string) => {
                finalOBject.ifIn.arrayNames.push(item); 
                finalOBject.ifIn.arrayItems.push(previousObject[item]['ifInUcastPkts']) ; 
                finalOBject.ifOut.arrayNames.push(item); 
                finalOBject.ifOut.arrayItems.push(previousObject[item]['ifOutUcastPkts']) ; 
                finalOBject.ifSpeed.arrayNames.push(item); 
                finalOBject.ifSpeed.arrayItems.push(previousObject[item]['ifSpeed']); 
                finalOBject.TXBytes.arrayNames.push(item); 
                finalOBject.TXBytes.arrayItems.push(previousObject[item]['TXBytes']); 
                finalOBject.RXBytes.arrayNames.push(item); 
                finalOBject.RXBytes.arrayItems.push(previousObject[item]['RXBytes']); 
            })

            previousObjectEstado.push(
                { name: 'rssi', arrayItems: arrayRSSI, showItems: arrayRSSI.length > 0,  title:'rssi', color: CONSTCOLORS.COLOR_HTML.single },
                { name: 'rsrq', arrayItems: arrayRSRQ, showItems: arrayRSRQ.length > 0,  title:'rsrq', color: CONSTCOLORS.COLOR_HTML.single },
                { name: 'rsrp', arrayItems: arrayRSRP, showItems: arrayRSRP.length > 0,  title:'rsrp', color: CONSTCOLORS.COLOR_HTML.single },
            ),

            finalObjGRAPHS.value = finalOBject;
            finalObjESTADO.value = previousObjectEstado;
        }
        else{
            console.log('no hay datos')
        }
    })

}

const getServerDataGraph = () => {
    allData.value = [];
    axios.get(CONSTAPIS.APIS.server.getAllServGraph,{params:{ip:props.ip}}).then((item) => {        
        allData.value = item.data; 
        finalObjGRAPHS.value = {};
        let previousArray = item.data;

        if( previousArray.length > 0 ) {
            let previousObject:any = {};
            let finalOBject: any = {
                ifIn    : {arrayNames:[], arrayItems:[]},
                ifOut   : {arrayNames:[], arrayItems:[]},
                ifSpeed : {arrayNames:[], arrayItems:[]},
            };
            let arrayKeyObj:string[] = [];
            previousArray.forEach((element:any) => {
                let time            = new Date(element.fecha);
                let milliseconds    = time.getTime();

                let finalJSON = JSON.parse(element.info_snmp);

                if(finalJSON.index) {
                    let finalInfo = finalJSON.index;
                    finalInfo.forEach((info:any) => {
                        let name = info.ifDescr.split("\"")[1];
    
                        if(!previousObject[name]){
                            previousObject[name] = {}
                            arrayKeyObj.push(name)
                        }
    
                        if(!previousObject[name]['ifInUcastPkts']){
                            previousObject[name]['ifInUcastPkts'] = []
                        }
                        if(!previousObject[name]['ifOutUcastPkts']){
                            previousObject[name]['ifOutUcastPkts'] = []
                        }
                        if(!previousObject[name]['ifSpeed']){
                            previousObject[name]['ifSpeed'] = []
                        }
                        previousObject[name]['ifInUcastPkts'].push([milliseconds,parseInt(info.ifInUcastPkts)])
                        previousObject[name]['ifOutUcastPkts'].push([milliseconds,parseInt(info.ifOutUcastPkts)])
                        previousObject[name]['ifSpeed'].push([milliseconds,parseInt(info.ifSpeed)])
                    
                    })
                }
            })
            if(arrayKeyObj.length > 0) {
                arrayKeyObj.forEach((item:string) => {
                    finalOBject.ifIn.arrayNames.push(item); 
                    finalOBject.ifIn.arrayItems.push(previousObject[item]['ifInUcastPkts']) ; 
                    finalOBject.ifOut.arrayNames.push(item); 
                    finalOBject.ifOut.arrayItems.push(previousObject[item]['ifOutUcastPkts']) ; 
                    finalOBject.ifSpeed.arrayNames.push(item); 
                    finalOBject.ifSpeed.arrayItems.push(previousObject[item]['ifSpeed']);
                })
                finalObjGRAPHS.value = finalOBject;
            }
        }
        else{
            console.log('no hay datos')
        }
               
        // previousArray.forEach((element:LTE_GRAPH)=>{
        //     let time            = new Date(element.fecha);
        //     let milliseconds    = time.getTime();
        //     let name            = element.ifDescr.split("'")[1];
        //     let firstName       = name.split(" ")

        // })
    })
}

const getSNMPMODENDataGraph = () => {

    axios.get(CONSTAPIS.APIS.lte.graphsnmpmodem,{params:{ip:props.ip}}).then((item) => {

        if(item.data.length > 0){
            showInfoModem.value = true;
            item.data.forEach((item:LTE_TABLE) => {
                let time            = new Date(item.fecha);
                let milliseconds    = time.getTime();
                let rssiItem        = parseInt(item.rssi.split('d')[0]);
                let rsrpItem        = parseInt(item.rsrp.split('d')[0]);
                let rsrqItem        = parseInt(item.rsrq.split('d')[0]);
                rssi.value.push([milliseconds,rssiItem]);
                rsrp.value.push([milliseconds,rsrpItem]);
                rsrq.value.push([milliseconds,rsrqItem]);
            })                                    
        }
        else{ showInfoModem.value = false }
    })
}



// const pageStatus = async() => {
// let params = new URLSearchParams({
//     ip : props.ip
// })
//     const response = await fetch(`${CONSTAPIS.APIS.lte.getAllGraphData}?${params}`);
//     if (!response.ok) throw new Error("Error en la peticin");

//     const data = await response.json();
//     console.log(data)
//     console.log(response)
// }

onMounted(async()=>{
    props.type === CONSTSTRINGS.STRINGS_HTML.LTE ? getLTEDataGraph()  : getServerDataGraph();   
    // props.type === CONSTSTRINGS.STRINGS_HTML.LTE ? getSNMPMODENDataGraph() : null;        
    // pageStatus()
})

</script>