<template>
        
    <v-sheet v-if="showGraph && finalTOTArray.length !== 0" :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="ma-4 pb-0" color="bgtopology">
        <v-row class="ma-0" v-if="finalTOTArray.length !== 0">
            <v-col cols="12" class="pa-0 pr-2" align-self="center">
                <v-sheet color="transparent" class="text-center">
                    <SingleChart v-if="!isLoadingData" :name="CONSTSTRINGS.STRINGS_HTML.Status" :first-array="finalArray" :title=" t('title.statuLog')" color="#0E2F5A"/>      
                    <span v-else class="loader"></span>           
                </v-sheet>
            </v-col>
        </v-row>
        <v-sheet v-else class="text-center" color="transparent">
            <Prog_circular />
        </v-sheet>
    </v-sheet>
        
</template>

<script setup lang="ts">
import { Ref, onMounted, ref } from 'vue';
import axios            from 'axios'
import { useDisplay }   from 'vuetify';
import { useI18n }       from 'vue-i18n';

// COMPONENTS 
import Prog_circular    from '@/components/PrincipalComponents/Prog_circular.vue';
import SingleChart      from '@/components/SmartLink/charts/SingleChart.vue';

// CONSTANTS
import CONSTAPIS        from '@/constants/apis';
import CONSTSTRINGS     from '@/constants/strings';

// INTERFACES
import { STATUSLOG }    from '@/interfaces/SmartLink/Graphs';

const props = defineProps({
    ip          : { required:true, type:String },
    typesel     : { required:true, type:String },
    name        : { required:true, type:String },
})

//==================================================//
//==================== VARIABLES ===================//
//==================================================//

/**
 * Vue / Vuetify
 */
const { t }                         = useI18n();
const { xs }                        = useDisplay();

/**
 * Component
 */
const finalArray:Ref<number[][]>    = ref([]);
const finalTOTArray:Ref<number[][]> = ref([]);
const showGraph                     = ref(true);
const totalCount                    = ref(0);
const timeStop                      = ref(300);
const isLoadingData                 = ref(false);

//==================================================//
//===================== METHODS ====================//
//==================================================//

/**
 * Get All data From API
 */

const params = { ip : props.ip };

const getData = async () =>{
    resetData()
    await axios.get(CONSTAPIS.APIS.topology.getStatusLog,{params:params}).then((item) => {
        if(item.data.length === 0){
            showGraph.value = false;
        }else{
            showGraph.value = true;
            item.data.forEach((element:STATUSLOG)=>{
                let tiempo          = new Date(element.fecha);
                let milliseconds    = tiempo.getTime();
                totalCount.value    += 1;
                if(element.status.toUpperCase() === CONSTSTRINGS.STRINGS_STATUS.OKMayus)      { finalTOTArray.value.push([milliseconds,3]) }
                if(element.status.toUpperCase() === CONSTSTRINGS.STRINGS_STATUS.ALEMAYS)   { finalTOTArray.value.push([milliseconds,2]) }
                if(element.status.toUpperCase() === CONSTSTRINGS.STRINGS_STATUS.ALAMAYS)   { finalTOTArray.value.push([milliseconds,1]) }
                if(element.status.toUpperCase() === CONSTSTRINGS.STRINGS_STATUS.DOWMAYS)    { finalTOTArray.value.push([milliseconds,0.2]) }
                if(element.status.toUpperCase() === CONSTSTRINGS.STRINGS_STATUS.Nodata)  { finalTOTArray.value.push([milliseconds,-1]) }
            })
            getTotalData();
        }        
    })           
}

const getTotalData = () =>{
    finalArray.value            = [];
    isLoadingData.value         = true;
    setTimeout(()=>{
        finalArray.value        = finalTOTArray.value ;
        isLoadingData.value     = false;
    },timeStop.value)
}

const resetData = () => { finalTOTArray.value = [] }

onMounted( async() => {
    getData();
})

</script>

<style>

.statusLogPercent .k-progress-outer{
    margin-right: 0px !important;
    padding-right: 0px !important;
}

.selectStatus:hover {
    cursor: pointer;
}

.loader {
  width: 48px;
  height: 48px;
  border: 5px solid #FFF;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  position: relative;
  animation: pulse 1s linear infinite;
}
.loader:after {
  content: '';
  position: absolute;
  width: 48px;
  height: 48px;
  border: 5px solid #FFF;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  animation: scaleUp 1s linear infinite;
}

@keyframes scaleUp {
  0% { transform: translate(-50%, -50%) scale(0) }
  60% , 100% { transform: translate(-50%, -50%)  scale(1)}
}
@keyframes pulse {
  0% , 60% , 100%{ transform:  scale(1) }
  80% { transform:  scale(1.2)}
}

</style>