<template>
    <v-sheet min-width="500" v-if="showPrimaryGraph" :class="xs?'card-dialogs-graph-wop':'card-dialogs-graph'" class="defaultCard" color="bgtopology">
        <v-row class="mx-1" justify='center'>
            <v-col class="pa-0 py-4"  style="max-width: max-content">
                <v-sheet  class="text-center" color="transparent" width="420" style="min-width: 420;"  v-if="showTUM">
                    <SingleCard 
                    :colorIcon="getColorStatus(status??'blue')"
                    :middleText="name"
                    :bottomText="ip"
                    :isTopology="true"
                    :typeItem="typesel"
                    />

                    <GaugeComponent
                    v-if="finalItemOperability.length > 0"
                    :recharge="false"
                    :array-items="finalItemOperability"
                    />
                </v-sheet>
            </v-col>

            <v-sheet color='transparent' class="ml-2" :style="smAndDown ? 'width: 100%' : 'width: calc(100% - 428px);'" style="align-content: center;">
                <SingleChart v-if="arrayValues.length !== 0" :name="'Latency'" :first-array="arrayValues" :title=" t('labels.latency')" color="#F6E5E5"/>
                <v-sheet v-else class="text-center" color="transparent">
                    <Prog_circular />
                </v-sheet>
            </v-sheet>
        </v-row>
    </v-sheet>
</template>

<script setup lang="ts">
import { Ref, ref, onMounted } from 'vue';
import axios                from 'axios'
import { useDisplay }       from 'vuetify';
import { useI18n }          from 'vue-i18n';

// COMPONENTS 
import SingleCard           from '@/components/SmartLink/topology/SingleCard.vue';
import Prog_circular        from '@/components/PrincipalComponents/Prog_circular.vue';
import SingleChart          from '@/components/SmartLink/charts/SingleChart.vue';
import GaugeComponent       from '@/components/SmartLink/dashboard/GaugeComponent.vue';

// CONSTANTS
import CONSTAPIS            from '@/constants/apis';
import CONSTCOLORS          from '@/constants/colors';

// INTERFACES
import { DEFAULT_GRAPH }    from '@/interfaces/SmartLink/Graphs'

const props = defineProps({
    ip          : { required:true, type:String },
    typesel     : { required:true, type:String },
    name        : { required:true, type:String },
    status      : { type:String}
})
        
const { t }     = useI18n();
const { xs, smAndDown }     = useDisplay();

const params = { 
    ip      : props.ip,
    type    : CONSTAPIS.APIS_STRINGS.latency 
};

const showTUM               = ref(false);
const showError             = ref(false);
const showPrimaryGraph      = ref(true);
const finalItemOperability  = ref();

const getData = async () =>{
    showError.value = false;
    showTUM.value   = false;
    await axios.get(CONSTAPIS.APIS.topology.getAllOperaByIP,{params:params}).then((item) => {
        if(item.data.length > 0){
            finalItemOperability.value = item.data
            showTUM.value   = true;
            showError.value = false;
        }else{
            showError.value = true;
            showTUM.value   = false
        }        
    })           
}

const arrayValues:Ref<number[][]>   = ref([]);

const getDataGraph = () =>{
    axios.get(CONSTAPIS.APIS.topology.getGraphTopo,{params:params}).then((item) => {
        if(item.data.length !== 0){
            showPrimaryGraph.value  = true;
            item.data.forEach((element:DEFAULT_GRAPH)=>{
                let tiempo          = new Date(element.fecha??'');
                let milliseconds    = tiempo.getTime();
                let values          = element.kpi??0;
                arrayValues.value.push([milliseconds,values]);
            })
        }
        else{
            showPrimaryGraph.value  = false
        }
    })
}

const getColorStatus = (estado:string) => {
    let finalColorStatus = '';
    if(estado?.toUpperCase() === 'OK')      finalColorStatus = CONSTCOLORS.COLOR_HTML.green;
    if(estado?.toUpperCase() === 'ALERT')   finalColorStatus = CONSTCOLORS.COLOR_HTML.yellow;
    if(estado?.toUpperCase() === 'ALARM')   finalColorStatus = CONSTCOLORS.COLOR_HTML.red;
    if(estado?.toUpperCase() === 'DOWN')    finalColorStatus = CONSTCOLORS.COLOR_HTML.grey;
    return finalColorStatus
}

onMounted( async() => {
    getData();
    getDataGraph();
    // getColorStatus;
})


</script>