const project = process.env.VUE_APP_NAME_PROJECT;
import { ref, Ref, computed }     from 'vue';
import { TOPO_GENERAL}  from '@/interfaces/SmartLink/Topology';
import { STRINGS }      from '@/interfaces/SmartLink/General';
import { vuetify } from '@/plugins/vuetify';

// STORE
import store from '@/store';

export function getNowTime(){
    let nowTime = new Date().getTime();
    if(project === 'SmartLinkMasterFront'){
        nowTime = new Date('2024-05-01 17:15:00').getTime();
    }
    return nowTime;
}

export function getFinDate(){
    let finDate = new Date();
    if(project === 'SmartLinkMasterFront'){
        finDate = new Date('2024-05-31 17:15:00');
    }
    return finDate;
}

export function getLatitude(latitud:string){
    let finalLatitud    = 0;
    const gradeLat      = parseFloat((latitud??'0').slice(0,2));
    const minuteLat     = (parseFloat((latitud??'0').slice(2,-1))/60);
    finalLatitud        = gradeLat + minuteLat;
    return finalLatitud
}

export function getLongitude(longitud:string){
    let finalLongitud   = 0;
    const gradeLng      = parseFloat((longitud??'0').slice(0,3));
    const minuteLng     = (parseFloat((longitud??'0').slice(3,-1))/60);
    finalLongitud       = (gradeLng + minuteLng) * (-1);
    return finalLongitud
}

export function getLatLngPrev(lat: string, lng: string){
    let position = {lat: 0, lng: 0};
    if(project === 'SmartLinkAntamina' || project === 'SmartLinkAntucoya'){
        position = {lat: parseFloat(lat), lng: parseFloat(lng)};
    }        
    if(project === 'SmartLinkMaster' || project === 'SmartLinkMasterFront'){
        const latSM     = (`${lat.slice(0,2)}.${lng.slice(2)}`);
        const lngSM     = (`${lat.slice(0,4)}.${lng.slice(4)}`);
        position        = {lat: parseFloat(latSM), lng: parseFloat(lngSM)};
    }
    return position;
}

export function getTimeZone(){
    let timezone = '';
    if(project === 'SmartLinkAntamina'){
        timezone = 'America/Bogota';
    }        
    if(project === 'SmartLinkMaster' || project === 'SmartLinkMasterFront' || project === 'SmartLinkCananea' ){
        timezone = 'America/Hermosillo';
    }
    if(project === 'SmartLinkAntucoya' || project === 'SmartLinkZaldivar' || project === 'SmartLinkCMDIC'){
        timezone = 'America/Santiago';
    }
    return timezone;
}

/**
 * Recibe el parametro que quiere que se muestre
 * Ejemplo: 'tipo', 'subtipo', 'name', etc
 * Retorna el objeto {
 *      ip : valor
 * }
 * @param selecteType 
 * @returns
 */
export function getInfoByIP(selecteType:string){
    
    const allDataInfo:TOPO_GENERAL[] = store.state.arrayItemsByStatus;
    const finalNameByIP:Ref<STRINGS> = ref({});
    const finalType: keyof TOPO_GENERAL = (selecteType as keyof TOPO_GENERAL) ?? 'ip';
    const allItems:TOPO_GENERAL[] = allDataInfo;

    allItems.forEach((item:TOPO_GENERAL) => {
        if(!finalNameByIP.value[item.ip]){
            finalNameByIP.value[item.ip] = ''
        }
        finalNameByIP.value[item.ip] = item[`${finalType}`];
    })
    
    return finalNameByIP.value;
}


interface Tooltip {
    formatter?: (this: { value?: number, y?:number, x?:number }) => string;
    valueDecimals? : number;
}





//captura el color del fondo y de las letras
const colorBG               = computed(() => vuetify.theme.global.current.value.colors.bgtopology);
const colorText             = computed(() => vuetify.theme.global.current.value.colors.colorText);

/**
 * Funcin que est diseada para retornar el objeto de la configuracin de los grficos.
 * Recibe varios parmetros con lgica ya hecha anteriormente, aqu solo lo junta.
 * @param typeChart Tipo de Chart (area, line, column, etc)
 * @param range Son las 4 Fechas seleccionables de visualizacin de Datos (Solo el nombre para hacerse con el i18n)
 * @param serie Los Valores de las Series con toda su informacin
 * @param title Si se recibe '', no se mostrar el titulo
 * @param subtitle Si se recibe '', no se mostrar el titulo
 * @param legendEnabled Habilita la leyenda de ser nesesario
 * @param height El tamao del grfico (el 'default' es 460)
 * @param tooltip Si recibe '{}', mostrar el tooltipo por defecto, sino sera uno personalizado
 * @param yaxis Si recibe '{}', no mostrar ningun valor en el yAxis, sino sera uno personalizado
 * @returns OBJETO de Opciones del CHART (no pude ponerle un TIPO)
 */
export function getOptionsInGraphs(typeChart:string,range:string[], serie:any, title:string, subtitle:string, legendEnabled:boolean, height:number, tooltip:Tooltip, yaxis:any){

    


    const finalOptions = {
        chart:{
            type: typeChart,
            height: height,
            backgroundColor: colorBG,
            style:{ color: colorText },
        },
        legend: {
            enabled: legendEnabled,
            align: 'center',
            layout: 'horizontal',
            verticalAlign: 'bottom',
            style:{ color: colorText }
        },        
        credits: {
            enabled: false
        },
        tooltip: tooltip,
        title: {
            text: title,
            style:{ color: colorText }
        },
        subtitle: {
            text: subtitle,
            style:{ color: colorText }
        },
        xAxis: {
            labels: {
                style:{ color: colorText }
            },
        },
        yAxis: yaxis,
        plotOptions: {
            area: {
                stacking: 'normal',
                lineColor: '#666666',
                lineWidth: 1,
                marker: {
                    lineWidth: 1,
                    lineColor: '#666666'
                }
            }
        },
        series: serie,
        rangeSelector: {
            allButtonsEnabled: true,
            buttons: [
                { type: 'day',      count: 1,   text: `1 ${range[0]}`}, 
                { type: 'day',      count: 2,   text: `2 ${range[0]}s` },
                { type: 'week',     count: 1,   text: `1 ${range[1]}` }, 
                { type: 'month',    count: 1,   text: `1 ${range[2]}` },
            ],
            buttonTheme: { width: 60 },
            selected: 0
        },
        exporting: { enabled: false },
        time: {
            timezone: getTimeZone()
        }
    }

    return finalOptions
}