<template>
    <div>
        <EasyDataTable
            show-index
            table-class-name="basicTable"
            :body-row-class-name="bodyRowClass"
            :headers="headers"
            :items="items"
            :rows-per-page="8"
            :hide-footer="hidden"
        >         
        </EasyDataTable>
    </div>
</template>

<script setup lang="ts">
import { computed, ref, Ref }    from 'vue';
import { useI18n }          from 'vue-i18n';

// COMPONENTS
// import  {Item, BodyRowClassNameFunction }   from "vue3-easy-data-table/types";

// INTERFACES
import { HEADER, ALLHEADER }                      from '@/interfaces/SmartLink/Tables';
import { STRINGS } from '@/interfaces/SmartLink/General';
// Definir el tipo para la funcin de clases de fila
type BodyRowClassNameFunction = (item: STRINGS, rowIndex: number) => string;

defineProps({        
    items   : { required:true },
    hidden  : { required:true, type:Boolean }
})
       
const { t }             = useI18n();
const headers:Ref<HEADER[]> = ref([]);
const HEADERS__PRIMARY_TABLE:ALLHEADER = {
    header : [
        { text: computed(() => t('labels.date')),       value: "fecha",         sortable: true },
        { text: computed(() => t('labels.ip')),         value: "ip",            sortable: true },
        { text: computed(() => t('labels.status')),     value: "status",        sortable: true },
        { text: computed(() => t('labels.cause')),      value: "causa",         sortable: true },
        { text: computed(() => t('labels.details')),    value: "detalle",       sortable: true },
    ]
}


headers.value = HEADERS__PRIMARY_TABLE['header']

// const bodyRowClass : any = (item:any):string =>{
const bodyRowClass : BodyRowClassNameFunction = (item:STRINGS):string =>{
    if(item.status.toUpperCase() === 'ALARM')  return 'alarm-design'
    if(item.status.toUpperCase() === 'ALERT')  return 'alert-design'
    return 'normal-design'
}

</script>

<style>
.alert-design {
  --easy-table-body-row-background-color: #fafad2;
  --easy-table-body-even-row-background-color: #fafad2;
}

.alarm-design{
  --easy-table-body-row-background-color: #eecaca;
  --easy-table-body-even-row-background-color: #eecaca;
}

.basicTable{
    
    --easy-table-header-background-color: #1E1E2F !important;
    --easy-table-header-font-color: white!important;
}

</style>