            btnDisabledFirst.value = true;

            headerQualLap.value     = HEADERS_TABLE_KPI[CONSTSTRINGS.STRINGS_VIS_PAGES.quality_lap];
            headerSNRVert.value     = HEADERS_TABLE_KPI[CONSTSTRINGS.STRINGS_VIS_PAGES.snr_vertical];
            headerSNRHorz.value     = HEADERS_TABLE_KPI[CONSTSTRINGS.STRINGS_VIS_PAGES.snr_horizontal];
            headerOperabi.value     = HEADERS_TABLE_KPI[CONSTSTRINGS.STRINGS_VIS_PAGES.operability];
            headerTenOperabi.value  = HEADERS_TABLE_KPI['tenlatency'];

