<template>
    <v-virtual-scroll 
    :items="items" 
    :max-height="height" 
    :class="'timelineStyles '+ classHiddenScroll" 
    v-click-outside="{
      handler: onClickOutside
    }"
    @click="activeClick"> 
        <template v-slot:default="{ item }">
            <v-sheet :width="width" color='bgtl' class="ma-2 timelineStyles">
                <p class="ml-3 mt-2">
                    <v-chip vlass="pl-3" :color="item.status.toUpperCase() === 'ALERT' ? CONSTCOLORS.COLOR_HTML.red :  CONSTCOLORS.COLOR_HTML.yellow" variant="flat">
                        {{ item.status.toUpperCase() === 'ALERT' ? t('labels.alert') : t('labels.alarm')  }}
                    </v-chip>
                    <span v-if="isRecurrent" class="ml-4"> {{ item.fecha }}</span>
                </p>
                <div v-if="isRecurrent">
                    <p class="textDashboard mt-4 ml-3"> <span> {{ t('labels.message') }}: </span> </p> 
                    <p class="textDashboard mt-2 ml-3 mb-3"> <span> {{ getMessage(item.causa, item.ip, item.detalle, item.recurrencia??0) }} </span> </p> 
                </div>
                <div v-else>
                    <p class="textDashboard mt-4 ml-3"> <span> {{ item.name }} - {{ item.ip }} - {{ item.fecha }}</span> </p> 
                    <p class="textDashboard mt-4 ml-3"> <span> {{ t('labels.cause') }}: {{ item.causa }} </span> </p> 
                    <p class="textDashboard ml-3 mb-3"> <span> {{ t('labels.detail') }}: {{ getDetalle(item.detalle) }} </span> </p> 
                    <!-- <p class="textDashboard mt-4 ml-3"> <span> {{ item.fecha }} </span> </p> -->
                </div>
            </v-sheet>
        </template>
    </v-virtual-scroll>    
</template>

<script setup lang="ts">
import { ref, Ref }     from 'vue';

// INTERFACES
import { ALARMTYPE }    from '@/interfaces/SmartLink/KPI';
import CONSTCOLORS from '@/constants/colors';
import { useI18n } from 'vue-i18n';
const { t }     = useI18n()

const props = defineProps({
    arrayItems  : { type:Array<ALARMTYPE>, required: true },
    width       : { type: Number, default: 300 },
    height      : { type: Number, default: 200 },
    isRecurrent : { type: Boolean, default: false}
})

const items:Ref<ALARMTYPE[]> = ref(props.arrayItems);


const getMessage = (causa:string, ip:string, detalle:string, recurrencia: number) => {
    let finalDetalle = JSON.parse(detalle)
    let messageFinal = `
    Se ha detectado ${causa} recurrente en el equipo ${ip} - ${finalDetalle.tipo_interferencia}: ${finalDetalle[finalDetalle.tipo_interferencia]}. Ha ocurrido ${recurrencia} veces.`;

    return messageFinal
}

const getDetalle = (detalle:string) => {
    let finalDetalle = JSON.parse(detalle)
    let messageFinal = `${finalDetalle.tipo_interferencia}: ${finalDetalle[finalDetalle.tipo_interferencia]}`;

    return messageFinal
}

const active = ref(false);
const onClickOutside = () => {
    active.value = false;
    classHiddenScroll.value = 'hiddenScroll timelineAlertCard'
}

const activeClick = () =>{
    active.value = true;
    classHiddenScroll.value = 'showScroll timelineAlertCardSelected';
}

const classHiddenScroll = ref('hiddenScroll timelineAlertCard')
</script>

<style>

.timelineStyles{
    border: 1px solid rgba(var(--v-theme-colorText),0.1);
    border-radius: 10px;
}

.timelineAlertCard:hover{
    border: 1.5px solid rgba(var(--v-theme-colorText),0.3);
}

.timelineAlertCardSelected{
    border: 2px solid rgba(var(--v-theme-colorText),0.7);
}

.showScroll{
    overflow: auto;
}

.hiddenScroll{
    overflow: hidden;
}
 
::-webkit-scrollbar{
    width: 4px;
}

/* ::-webkit-scrollbar:hover{
    width: 8px;
} */

::-webkit-scrollbar-track {
    background: transparent;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: rgba(var(--v-theme-colorText),.5); 
    border-radius: 10px; 
    visibility: visible;
}

.timelineStyles::-webkit-scrollbar-thumb {
    background: rgba(255,255,255,.2);
    border-radius: 10px; 
    visibility: hidden;
} 
.timelineStyles:hover::-webkit-scrollbar-thumb {
    background: rgba(255,255,255,.2);
    border-radius: 10px; 
    visibility: visible;
} 
/* .timelineStyles::-webkit-scrollbar:hover {
     width: 10px;
}  */

.v-timeline-item__opposite{
    padding: 0 !important;
}

</style>