        let ipArray:string[] = [];
        if(response.data.length > 0) {
            response.data.forEach((item:EQUIPAMENTS)=> {
                item.ip ? ipArray.push(item.ip) : null
            }) 
            ipVIRMAC.value = ipArray;
        }
    }); 
}

