        //     let firstName       = name.split(" ")

        // })
    })
}

const getSNMPMODENDataGraph = () => {

    axios.get(CONSTAPIS.APIS.lte.graphsnmpmodem,{params:{ip:props.ip}}).then((item) => {

        if(item.data.length > 0){
            showInfoModem.value = true;
            item.data.forEach((item:LTE_TABLE) => {
                let time            = new Date(item.fecha);
                let milliseconds    = time.getTime();
                let rssiItem        = parseInt(item.rssi.split('d')[0]);
                let rsrpItem        = parseInt(item.rsrp.split('d')[0]);
                let rsrqItem        = parseInt(item.rsrq.split('d')[0]);
                rssi.value.push([milliseconds,rssiItem]);
                rsrp.value.push([milliseconds,rsrpItem]);
                rsrq.value.push([milliseconds,rsrqItem]);
            })                                    
        }
        else{ showInfoModem.value = false }
    })
}



